.listicle {
  counter-reset: ama-listicle;
  list-style: none;
  @include gutter($padding-top-half...);
  @include gutter($padding-bottom-half...);

  &__item {

    counter-increment: ama-listicle;
    position: relative;
    margin-left: 43px;
    margin-bottom: 19px;
    @include gutter($margin-top-half...);

    &::before {
      content: counter(ama-listicle);
      position: relative;
      background: $homepagePurple;
      color: $white;
      width: 30px;
      height: 30px;
      top: 0;
      text-align: center;
      font-size: 22px;
      font-weight: 600;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      left: -43px;
      margin-right: -35px;
    }

    &-sub {
      list-style: none;
      padding-left: 0 !important;

      &-item {
        @include gutter($margin-bottom-half...);
        margin-top: 0;
      }
    }
    figure {
      float: right;
      margin-bottom: 19px;
      figcaption {
        margin-left: 1rem;
      }
      &.no-margin {
        float: none;
        display: block;
        width: 100%;
        figcaption {
          margin-left: 0rem;
          display: block;
        }
      }
      img {
        margin-top: 7px;
        &.no-margin {
          margin-left: 0
        }
        @include breakpoint($bp-xs) {
          width: 100%;
          margin-left: 0;
        }
        @include breakpoint($bp-med) {
          width: auto;
          margin-left: 1rem;
        }
      }
    }
  }

  &__title,
  h3,
  h2 {
    font-size: 22px;
    font-weight: 600;
    display: inline;
    width: 100%;
    @include gutter($margin-bottom-full...);

    a {
      font-weight: inherit;
      color: inherit;
    }
  }
  .clearfix {
    width: 100%;
    height: 1px;
    clear: both;
  }
}
