// Parameters
// $type => "secondary", ""
//

@mixin ama-button ($size:"", $style:"") {
  @include ama-button-base;

  @if($style == "secondary") {
    background-color: $white;
    border-color: $purple;
    color: $purple;

    &:hover {
      background-color: $purple;
      color: $white;
    }
  }

  @else if($style == "tertiary") {
    background-color: $white;
    border-color: $purple;
    color: $purple;

    &:hover {
      background-color: $hoverPurple;
      color: $white;
    }
  }
  //outline
  @else if($style == "reset") {
    background-color: transparent;
    border-color: transparent;
    color: $purple;

    &:hover {
      background-color: transparent;
      border-color: transparent;
      color: $hoverPurple;
      text-decoration: underline;
    }
  }
  @else if($style == "link") {
    background-color: transparent;
    border-color: transparent;
    color: $blue;

    &:hover {
      background-color: transparent;
      border-color: transparent;
      color: $hoverBlue;
      text-decoration: underline;
    }
  }
  // homepage
  @else if($style == "homepage") {
    background-color: $homepagePurple;
    border-color: transparent;
    color: $white;
    text-transform: uppercase;

    &:hover {
      background-color: $hoverPurple;
      border-color: transparent;
      color: $white;
      text-decoration: none;
    }
  }
  // homepage secondary
  @else if($style == "homepage-secondary") {
    background-color: $white;
    border-color: transparent;
    color: $purple;
    text-transform: uppercase;

    &:hover {
      background-color: $white;
      border-color: transparent;
      color: $hoverPurple;
      text-decoration: none;
    }
  }
  @else if($style == "promo") {
    background-color: $orange;
    border-color: transparent;
    color: $black;

    &:hover {
      background-color: $hoverOrange;
      border-color: transparent;
      color: $black;
      text-decoration: none;
    }
  }
  @else {
    background-color: $purple;
    border-color: transparent;
    color: $white;

    &:hover {
      background-color: $hoverPurple;
    }
  }

  @if($size == "small" and $style == "secondary") {
    @include type($myriad-pro, $font-weight-regular);
    @include font-size($button-font-sizes--menu);
    padding: $gutter/6 $gutter/2;
    background-color: $white;
    border-color: $purple;
    color: $purple;

    &:hover {
      background-color: $purple;
      color: $white;
    }
  }
  @else if($size == "small") {
    @include gutter-all($padding-all-half...);
  }
  @elseif ($size == "block") {
    display: block;
    width: 100%;
  }
  @else {
   display: inline-block;
  }
}
